.menu-toggle {
  position: relative;
  display: none;
  width: 65px;
  height: 65px;
  background-color: #ffffff;
  border: none;
  outline: none;
  cursor: pointer;
}
.menu-toggle::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 25px;
  height: 2px;
  background-color: #222222;
  box-shadow: #222222 0 -6px 0 0, #222222 0 6px 0 0;
}
.menu-toggle--cross::before {
  top: 47%;
  left: 32%;
  transform: rotate(45deg);
  width: 24px;
  box-shadow: none;
}
.menu-toggle--cross::after {
  position: absolute;
  content: "";
  top: 47%;
  left: 32%;
  transform: rotate(-45deg);
  width: 24px;
  height: 2px;
  background-color: #222222;
}
.menu-toggle:hover::before {
  background-color: rgba(34, 34, 34, 0.8);
  box-shadow: rgba(34, 34, 34, 0.8) 0 -6px 0 0, rgba(34, 34, 34, 0.8) 0 6px 0 0;
}
.menu-toggle--cross:hover::before,
.menu-toggle--cross:focus::before {
  box-shadow: none;
}
.menu-toggle--cross:hover::after,
.menu-toggle--cross:focus::after {
  background-color: rgba(34, 34, 34, 0.8);
}
.menu-toggle:active::before {
  background-color: rgba(34, 34, 34, 0.6);
  box-shadow: rgba(34, 34, 34, 0.6) 0 -6px 0 0, rgba(34, 34, 34, 0.6) 0 6px 0 0;
}
.menu-toggle--cross:active::before {
  box-shadow: none;
}
.menu-toggle--cross:active::after {
  background-color: rgba(34, 34, 34, 0.6);
}
.menu-toggle__text {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (max-width: 767px) {
  .menu-toggle--show {
    display: block;
  }
}
